<template>
  <div class="box">

    <div class="container">
      <div class="child">
        <div class="tip">请将声明好的代码粘入下边输入框</div>
        <el-input class="inputView" type="textarea" :rows="15" resize="none" placeholder="请输入内容" v-model="inputText"
          @input="handleInputChange">
        </el-input>
      </div>

      <div class="child">
        <el-button type="primary" class="input_btn" @click="copyBtnClick()">点击复制</el-button>
        <el-input class="inputView" type="textarea" :rows="12" resize="none" placeholder="请等待结果" v-model="resText">
        </el-input>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  components: {

  },
  data () {
    return {
      inputText: '',
      resText: '',
      checked: false
    }
  },
  methods: {
    handleCheckAllChange () {
      if (this.checked === true) {
        console.log('选中了')
        this.$alert('选中后会在生成的懒加载中尽可能多的默认代码(暂未开发完成)', '提示', {
          confirmButtonText: '确定',
          callback: action => {

          }
        })
      } else {
        console.log('取消了')
      }
    },
    copyBtnClick () {
      if (this.resText.length === 0) {
        this.$message({
          message: '文本内容不能为空',
          type: 'warning'
        })
        return
      }

      const tempInput = document.createElement('textarea')
      tempInput.value = this.resText
      // 将临时文本框添加到页面中
      document.body.appendChild(tempInput)
      // 选中文本内容
      tempInput.select()
      // 执行复制操作
      document.execCommand('copy')
      // 移除临时文本框
      document.body.removeChild(tempInput)

      this.$message({
        message: '复制成功',
        type: 'success'
      })
    },

    handleInputChange (value) {
      this.resText = ''
      const allList = []
      const text = value
      const lines = text.split('\n')
      lines.forEach((line) => {
        // 移除无用代码
        const lineStr = line.replace(/@property\s*\([^)]*\)\s*/, '')
        if (lineStr.includes('*')) {
          const arr = lineStr.split(/\s+/).map(item => item.replace('*', ''))
          allList.push(arr)// 输出 ["NSTextView", "*inputTextView;"]
        }
        // this.resText = this.resText.length === 0 ? lineStr : this.resText + '\n' + lineStr
      })
      // 拼接
      const resArray = []
      allList.forEach((arr) => {
        const title = arr[0]
        const detail = arr[1]
        const detail2 = detail.replace(';', '')
        if (title === 'UIButton') {
          const tmpStr = '- (%@ *)%! {\n    if (_%! == nil) {\n        _%! = [%@ buttonWithType:UIButtonTypeCustom];\n    }\n    return _%!;\n}'
          const newStr = tmpStr.replace(/%@/g, title)
          const newStr2 = newStr.replace(/%!/g, detail2)
          resArray.push(newStr2)
        } else if (title === 'UITableView') {
          const tmpStr = '- (%@ *)%!{\n    if (_%! == nil) {\n        _%! = [[%@ alloc] initWithFrame:CGRectZero style:UITableViewStylePlain];\n    }\n    return _%!;\n}'
          const newStr = tmpStr.replace(/%@/g, title)
          const newStr2 = newStr.replace(/%!/g, detail2)
          resArray.push(newStr2)
        } else if (title === 'UICollectionView') {
          const tmpStr = '- (%@ *)%!{\n    if (_%! == nil) {\n        _%! = [[%@ alloc] initWithFrame:CGRectZero collectionViewLayout:<#(nonnull UICollectionViewLayout *)#>];\n    }\n    return _%!;\n}'
          const newStr = tmpStr.replace(/%@/g, title)
          const newStr2 = newStr.replace(/%!/g, detail2)
          resArray.push(newStr2)
        } else {
          const tmpStr = '- (%@ *)%!{\n    if (_%! == nil) {\n        _%! = %@.alloc.init;\n    }\n    return _%!;\n}'
          const newStr = tmpStr.replace(/%@/g, title)
          const newStr2 = newStr.replace(/%!/g, detail2)
          resArray.push(newStr2)
        }
      })

      resArray.forEach((str) => {
        this.resText = this.resText + '\n' + str
      })
      console.log(this.resText)
    }
  }
}
</script>

